import React, {useEffect, useState} from 'react'
import Die from './../Die'
import './style.scss'

const DiceSet = (props) => {
  const {dice, players, currentPlayerIndex, updateLog, rollIndex, turns, refs} = props
  const [rolling, setRolling] = useState(true)
  const diceValues = dice.map(d => d.value)
  
  useEffect(() => {
    updateLog(`${players[currentPlayerIndex]} rolled ${diceValues.join(', ')}`)
    setRolling(true)
    setTimeout(() => {
      setRolling(false)
    }, 500)
  }, [rollIndex, currentPlayerIndex, turns]) //eslint-disable-line

  const onDieClick = (i) => {
    const nextDice = [...dice]
    nextDice[i].saved = !nextDice[i].saved
    props.setDice(nextDice)
  }

  return (
    <div className="Dice">
      {dice.map((die, i) => <Die
        refRef={refs[i]}
        key={i}
        value={die.value}
        saved={die.saved}
        rolling={rolling}
        onClick={() => {onDieClick(i)}}
      />)}
    </div>
  )
}

export default DiceSet