import React, {useState, useEffect, useRef} from 'react'
import { useHotkeys } from 'react-hotkeys-hook';
import Players from './../Players'
import DiceSet from './../DiceSet'
import Scoreboard from './../Scoreboard'
import _ from 'lodash'
import './style.scss'

const Game = (props) => {
  const makeDie = () => {
    return {saved: false, value: _.random(1, 6)}
  }

  const makeDice = () => {
    return new Array(5).fill(null).map(makeDie)
  }

  const [turnPlayed, setTurnPlayed] = useState(false)
  const [gameStarted, setGameStarted] = useState(true)
  const [gameOver, setGameOver] = useState(false)
  const [players, setPlayers] = useState([])
  const [currentPlayerIndex, setCurrentPlayerIndex] = useState(0)
  const [rollIndex, setRollIndex] = useState(0)
  const [dice, setDice] = useState(makeDice())
  const [turns, setTurns] = useState(0)
  const [log, setLog] = useState([])
  const [scores, setScores] = useState({})
  const [finalScores, setFinalScores] = useState(null)
  const logRef = useRef(null)
  const nextTurnButtonRef = useRef(null)
  const rollAgainButtonRef = useRef(null)
  const dice1Ref = useRef(null)
  const dice2Ref = useRef(null)
  const dice3Ref = useRef(null)
  const dice4Ref = useRef(null)
  const dice5Ref = useRef(null)

  useEffect(() => {
    if (players.length) {
      setGameStarted(true)
    }
  }, [players])

  useEffect(() => {
    if (!gameStarted)
      return
    if (!players.length)
      return
    if (!log.length) {
      updateLog(`Keyboard commands enabled.`)
      updateLog(`Press ? to view commands`)
      updateLog(``)
      updateLog(`Game started with ${players.length} players:`)
      updateLog(`${players.join(', ')}`)
      updateLog(``)
    }
  }, [turns, gameStarted, players, players.length, log])

  useEffect(() => {
    if (!logRef) {
      return
    }
    if (!logRef.current) {
      return
    }
    logRef.current.scrollTop = logRef.current.scrollHeight
  }, [log])

  useEffect(() => {
    if (!players.length) return
    if (gameOver) return
    let total = 0
    for (const k1 of Object.keys(scores)) {
      for (const k2 of Object.keys(scores[k1])) {
        const score = scores[k1][k2]
        total += score.assignable && score.actual !== null ? 1 : 0
      }
    }
    if (total === players.length * 13) {
      updateLog(`GAME OVER!`)
      const finalScores = []
      for (const player of players) {
        finalScores.push({player, score: scores[player].total.actual})
        updateLog(`${player} has a total score of ${scores[player].total.actual}`)
      }
      finalScores.sort((a, b) => a.score > b.score ? -1 : 1)
      updateLog(`${finalScores[0].player} wins! üèÜ`)
      setFinalScores(finalScores)
      setGameOver(true)
    }
  }, [scores, gameOver, players])

  useHotkeys('1', () => dice1Ref.current && dice1Ref.current.click(), {keydown: true, keyup: false})
  useHotkeys('2', () => dice2Ref.current && dice2Ref.current.click(), {keydown: true, keyup: false})
  useHotkeys('3', () => dice3Ref.current && dice3Ref.current.click(), {keydown: true, keyup: false})
  useHotkeys('4', () => dice4Ref.current && dice4Ref.current.click(), {keydown: true, keyup: false})
  useHotkeys('5', () => dice5Ref.current && dice5Ref.current.click(), {keydown: true, keyup: false})
  useHotkeys('r', () => rollAgainButtonRef.current && rollAgainButtonRef.current.click())
  useHotkeys('n', () => nextTurnButtonRef.current && nextTurnButtonRef.current.click())
  useHotkeys('shift+/', () => showKeyboardCommands())

  const showKeyboardCommands = () => {
    updateLog(`Keyboard commands enabled:`)
    updateLog(`‚Ä¢ R - roll again`)
    updateLog(`‚Ä¢ N - next turn`)
    updateLog(`‚Ä¢ [1,2,3,4,5] - toggle selected dice`)
    updateLog(`‚Ä¢ OPTION+[1-6,T,F,H,S,L,Y,C] - enter score`)
    updateLog(`‚Ä¢ CTRL+Z - undo last entry (in current turn)`)
    updateLog(``)
  }

  const startGame = (players) => {
    setPlayers(players)
  }

  const onNextTurn = () => {
    setCurrentPlayerIndex(turns % players.length)
    setTurns(turns + 1)
    rollDice(true)
  }

  const updateLog = (line) => {
    setLog(prevLog => [...prevLog, line])
  }

  const rollDice = (resetDice = null) => {
    const nextDice = resetDice
      ? makeDice()
      : [...dice].map((die) => {
          die.value = !die.saved ? _.random(1, 6) : die.value
          return die
        })
    setDice(nextDice)
    setRollIndex(resetDice ? 0 : rollIndex + 1)
  }

  const renderGame = () => {
    return (
      <div className="Game">
        <div className="Game-Playfield">
          {turns === 0 &&
            <div className={`Game-Playfield-StartGame`}>
              <button onClick={onNextTurn}>
                Player 1 go!
              </button>
            </div>
          }
          {gameOver &&
            <div className="Game-Playfield-Fragment">
              <div className="Game-Playfield-GameOver">
                <div><strong>{`${finalScores[0].player} wins! üèÜ`}</strong></div>
                <div>{`Good game, ${finalScores[0].player}!`}</div>
                <table>
                  <tbody>
                    {finalScores.map((fs, i) =>
                      <tr key={i}>
                        <td>{`#${i+1}`}</td>
                        <td>{fs.player}</td>
                        <td>{`${fs.score} points`}</td>
                      </tr>
                    )}
                  </tbody>
                </table>
              </div>
            </div>
          }
          {!gameOver && rollIndex >= 0 && turns > 0 &&
            <div className="Game-Playfield-Fragment">
              <div className={`Game-Playfield-TurnIndicator ${rollIndex === 2 ? 'last' : ''}`}>
                <div className="Game-Playfield-TurnIndicator-Player">{`${players[currentPlayerIndex]}'s turn`}</div>
                <div className="Game-Playfield-TurnIndicator-Roll">{`Roll # ${rollIndex + 1}`}</div>
              </div>
              <div className="Game-Playfield-Dice">
                <DiceSet
                  turns={turns}
                  players={players}
                  currentPlayerIndex={currentPlayerIndex}
                  rollIndex={rollIndex}
                  setDice={setDice}
                  dice={dice}
                  updateLog={updateLog}
                  refs={[dice1Ref, dice2Ref, dice3Ref, dice4Ref, dice5Ref]}
                />
              </div>
              <div className="Game-Playfield-Buttons">
                <button
                  ref={rollAgainButtonRef}
                  onClick={() => rollDice(false)}
                  disabled={rollIndex > 1 || turnPlayed}
                >
                  roll again!
                </button>
                <button
                  ref={nextTurnButtonRef}
                  onClick={onNextTurn}
                  disabled={!turnPlayed || players.length * 13 === turns}
                >
                  next turn!
                </button>
              </div>
            </div>
          }
          <div className="Game-Playfield-Log">
            <textarea ref={logRef} value={log.join('\n')} />
          </div>
        </div>
        {gameStarted &&
          <div className="Game-Scoreboard">
            <Scoreboard
              gameOver={gameOver}
              turns={turns}
              scores={scores || {}}
              setScores={setScores}
              turnPlayed={turnPlayed}
              setTurnPlayed={setTurnPlayed}
              setTurns={setTurns}
              rollIndex={rollIndex}
              players={players}
              currentPlayerIndex={currentPlayerIndex}
              dice={dice}
              log={log}
              updateLog={updateLog}
            />
          </div>
        }
      </div>
    )
  }

  return !players.length
    ? <Players startGame={startGame} />
    : renderGame()
}

export default Game
