import React, {useState, useRef} from 'react'
import {emojis} from './constants'
import './style.scss'
import { useEffect } from 'react'

const Players = (props) => {
  const [players, setPlayers] = useState([])
  const [player, setPlayer] = useState('')
  const [emoji, setEmoji] = useState('')
  const playerInputRef = useRef(null)

  useEffect(() => {
    playerInputRef.current.focus()
  }, [])

  const addPlayer = (event) => {
    event.preventDefault()
    const nextPlayer = `${emoji} ${player}`
    if (!player || players.includes(nextPlayer) || !emoji)
      return
    setPlayers([...players, nextPlayer])
    setPlayer('')
    setEmoji('')
    playerInputRef.current.focus()
  }

  const changePlayer = (event) => {
    setPlayer(event.target.value)
  }

  const savePlayers = () => {
    props.startGame(players)
  }

  const selectEmoji = (emoji) => {
    setEmoji(emoji)
    playerInputRef.current.focus()
  }

  return (
    <div className="Players">
      <p>Who's playing Yhatzee?</p>
      <form onSubmit={addPlayer}>
      <div className="emojis">
          {emojis.map(e =>
            <div
              key={e}
              className={`emojis-emoji ${e === emoji ? 'emojis-emoji--selected' : ''}`}
              onClick={() => selectEmoji(e)}
            >
              {e}
            </div>
          )}
        </div>
        <input
          name="player"
          value={player}
          placeholder={`Player ${players.length + 1}`}
          onChange={changePlayer}
          ref={playerInputRef}
        />
        <button type="submit" disabled={!player || !emoji}>Add Player</button>
      </form>
      <ul>
        {players.map(player =>
          <li key={player}>{player}</li>
        )}
      </ul>
      {!!players.length &&
        <button onClick={savePlayers}>start game</button>
      }
    </div>
  )
}

export default Players
